
import Epub from 'epubjs'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: 'rViewer',
directives: {
TransferDom
},
components: {
Popup
},
data () {
return {
showMenus: false, // 是否显示底部菜单栏
showContents: false, // 是否显示目录
showProgress: false, // 是否显示进度条
showTools: false, // 是否显示设置菜单
book: null, // 文本对象（即当前阅读的书籍所映射的对象
bookTitle: null, // 书籍名称
rendition: null, // 阅读器文本操作对象
navigation: null, // 阅读器目录对象
fontSize: 16,
curFontColor: 'white', // 当前选择的背景色，默认为白色样式
themesList: [
{
name: 'light-blue',
style: {
body: {
'color': '#000',
'background': '#eefaff'
}
}
},
{
name: 'green',
style: {
body: {
'color': '#000',
'background': '#edf6d0'
}
}
},
{
name: 'earth-yellow',
style: {
body: {
'color': '#000',
'background': '#f6f1e7'
}
}
},
{
name: 'black',
style: {
body: {
'color': '#fff',
'background': '#222'
}
}
},
{
name: 'white',
style: {
body: {
'color': '#000',
'background': '#f9f9f9'
}
}
}
]
}
},
mounted () {
console.log('view mounted')
this.initReader()
},
methods: {
initReader () {
const vm = this

const url = 'static/超级时间.epub'
vm.book = new Epub(url)
console.log('vm.book', vm.book)
vm.rendition = vm.book.renderTo('read', {
width: window.innerWidth,
height: window.innerHeight
})
vm.rendition.display()

if (vm.rendition.themes) {
vm.themesList && vm.themesList.forEach(item => {
vm.rendition.themes.register(item.name, item.style)
})

vm.rendition.themes.select('white')
}
},
nextPage () {
let vm = this
if (vm.rendition) {
vm.rendition.next()
}
},
prevPage () {
let vm = this
if (vm.rendition) {
vm.rendition.prev()
}
},
onClick (e) {
let vm = this
if (vm.showMenus) {
vm.showMenus = false
return
}
if (vm.showContents) {
vm.showContents = false
return
}
if (vm.showTools) {
vm.showTools = false
return
}

let clintWidth = document.body.clientWidth
let unit = clintWidth / 8
console.log('unit=', 3 * unit)
if (e.offsetX < (3 * unit)) {
vm.prevPage()
} else if (e.offsetX > (5 * unit)) {
vm.nextPage()
} else {
vm.showMenus = true
}
},
onClickMenus (type) {
let vm = this
vm.showMenus = false
switch (type) {
case 'contents':
vm.navigation = vm.book.navigation
vm.showContents = true
vm.bookTitle = vm.book.package.metadata.title
console.log('vm.book=', vm.book)
break
case 'progress':
vm.showProgress = true
break
case 'tools':
vm.showTools = true
break
default:
break
}
},
onfontAdd () {
let vm = this
if (vm.fontSize >= 30) return false

vm.fontSize += 2
vm.rendition.themes.fontSize(vm.fontSize + 'px')
},
onfontMinus () {
let vm = this
if (vm.fontSize <= 13) return false

vm.fontSize -= 2
vm.rendition.themes.fontSize(vm.fontSize + 'px')
},
onSelectTheme (name) {
let vm = this
vm.curFontColor = name
if (vm.rendition.themes) {
vm.rendition.themes.select(name)
}
},
onToChapter (href) {
let vm = this
if (vm.rendition) {
console.log('href=', href)
vm.rendition.display(href)
vm.showContents = false
}
}
}
}



